.container__inner{
  width: 80%;
  margin: 0 auto;
  ul{
    padding-left: 38px;
    margin-top: 14px;
    li{
      font-weight: normal;
      font-size: 14px;
      line-height: 26px;
      letter-spacing: 0.01em;
      color: #333333;
      position: relative;
      margin-bottom: 12px;
      &:after{
        position: absolute;
        content: '';
        width: 9px;
        height: 9px;
        background: $blue;
        border-radius: 50%;
        left: -18px;
        top: 8px;
      }
    }
    li:nth-child(even){
      &:after{
        background: transparent;
        border: .5px solid $blue;
      }
    }
  }
  h4{
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    color: #222222;
    margin-bottom: 10px;
    margin-top: 20px;
  }
  p{
    font-weight: normal;
    font-size: 14px;
    line-height: 28px;
    letter-spacing: 0.01em;
    color: #333333;
    margin: 20px 0;
  }
  a{
    color: $blue;
    font-weight: 400;
    &:hover{
      text-decoration: underline;
    }
  }
  img{
    display: block;
    margin: 12px auto;
    max-width: 80%;
  }
}

.global__articles{
  margin-top: 40px;
}

.back{
  margin-top: 80px;
  margin-bottom: 100px;
}



@media( max-width:1000px) and (min-width: 801px){
  .container__inner{
    width: 100%;
  }

  .global__articles{
    margin-top: 60px;
  }
}


@media( max-width:800px) and (min-width: 601px){

  .global__articles{
    margin-top: 150px;
  }

  .container__inner{
    width: 100%;
  }

  .bread__crumbs{
    span{
      margin-bottom: 14px;
    }
  }

}

@media( max-width:600px){
  .global__articles{
    margin-top: 190px;
  }

  .container__inner{
    width: 100%;
  }

}
