.compare{
  &__wrap{
    margin-bottom: 60px;
  }
  .back{
    padding-left: 10px;
    a{
      font-weight: normal;
      font-size: 14px;
      line-height: 24px;
      color: #222222;
    }
  }
  table{
    td{
      .item{
        display: -webkit-flex;
        display: -ms-flex;
        display: flex;
        flex-direction: column;
        width: 300px;
        padding-bottom: 20px;
        &__header{
          display: -webkit-flex;
          display: -ms-flex;
          display: flex;
          margin-bottom: 10px;
          .title{
            font-weight: normal;
            font-size: 12px;
            line-height: 24px;
            color: $blue;
            width: 80%;
          }
          a.close{
            &:before, &:after{
              width: 20px;
            }
          }
        }
        &__content{
          display: -webkit-flex;
          display: -ms-flex;
          display: flex;
          align-items: center;
          &-img{
            width: 65px;
            height: 65px;
            background-color: #F5F7FF;
            background-size: 85%;
            background-position: center;
            background-repeat: no-repeat;
            margin-right: 30px;
          }
          &-inf{
            .price{
              font-weight: 600;
              font-size: 16px;
              line-height: 20px;
              color: $blue;
            }
            .status{
              font-weight: 300;
              font-size: 14px;
              line-height: 17px;
              color: #c4c4c4;
              margin-top: 0;
            }
          }
        }
        &__parameters{
          margin-top: 40px;
          width: 300px;
          font-weight: normal;
          font-size: 12px;
          line-height: 15px;
          color: $blue;
          &.main{
            font-weight: normal;
            font-size: 12px;
            line-height: 24px;
            color: #333;
          }
        }
      }
    }
  }
}

html, body::-webkit-scrollbar {
    width: 8px; /* ширина для вертикального скролла */
    height: 8px; /* высота для горизонтального скролла */
    margin-right: 10px;
}
html::-webkit-scrollbar-thumb, body::-webkit-scrollbar-thumb {
    background: $blue;
    border-radius: 10px;
}


@media (max-width: 1000px) and (min-width: 801px){
  .compare__wrap{
    overflow-x: scroll;
    padding-bottom: 20px;
    &::-webkit-scrollbar {
        height: 8px;
        width: 10%;
    }
    &::-webkit-scrollbar-thumb {
        background: $blue;
        border-radius: 10px;
    }
  }
}

@media (max-width: 800px) and (min-width: 601px){
  .compare{
    margin-top: 100px;
  }
  .compare__wrap{
    overflow-x: scroll;
    padding-bottom: 20px;
    &::-webkit-scrollbar {
        height: 8px;
        width: 10%;
    }
    &::-webkit-scrollbar-thumb {
        background: $blue;
        border-radius: 10px;
    }

  }
}

@media (max-width: 600px) {
  .compare{
    margin-top: 190px;
  }
  .compare__wrap{
    overflow-x: scroll;
    padding-bottom: 20px;
    &::-webkit-scrollbar {
        height: 8px;
        width: 10%;
    }
    &::-webkit-scrollbar-thumb {
        background: $blue;
        border-radius: 10px;
    }
  }
}
