.checkout{
  &__product{
    &-wrap{
      table{
        border-spacing: 0 24px;
        width: 100%;
        text-align: center;
      }
    }
    &-header{
      font-weight: 500;
      font-size: 12px;
      line-height: 15px;
      letter-spacing: 0.01em;
      color: #333333;
      font-family: 'Montserrat', sans-serif;
      .checkout{
        &__photo{
          width: 10%;
        }
        &__name{
          width: 30%;
        }
        &__article{
          width: 15%;
        }
        &__count{
          width: 15%;
        }
        &__price{
          width: 15%;
        }
        &__total{
          width: 15%;
        }
      }
    }
    tbody{
      padding-top: 30px;
    }
    &-item{
      margin-top: 20px;
      .checkout{
        &__photo{
          width: 10%;
          .checkout__photo-img{
            width: 56px;
            height: 56px;
            background-color: #F5F7FF;
            background-size: 85%;
            background-position: center;
            background-repeat: no-repeat;
            margin-left: 20px;
          }
        }
        &__name{
          width: 30%;
          font-weight: normal;
          font-size: 11px;
          line-height: 13px;
          color: #222222;
        }
        &__article{
          width: 15%;
          font-weight: normal;
          font-size: 11px;
          line-height: 13px;
          color: rgba(66, 73, 226, 0.8);
        }
        &__count{
          width: 15%;
          button{
            border: none;
            background: transparent;
            color: #ddd;
            font-family: 'Montserrat', sans-serif;
            font-size: 20px;
            cursor: pointer;
          }
          input{
            border: none;
            background: transparent;
            color: $blue;
            font-weight: 500;
            font-size: 15px;
            line-height: 18px;
            font-family: 'Montserrat', sans-serif;
            width: 20px;
            text-align: center;
          }
        }
        &__price{
          width: 15%;
          font-weight: 600;
          font-size: 13px;
          line-height: 16px;
          color: rgba(66, 73, 226, 0.8);
        }
        &__total{
          width: 15%;
          font-weight: 600;
          font-size: 13px;
          line-height: 16px;
          color: rgba(66, 73, 226, 0.8);
        }
      }
    }
  }
}

.checkout__product-total{
  margin-top: 24px;
  .checkout__total-item{
    p{
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      justify-content: flex-end;
      font-weight: 500;
      font-size: 13px;
      line-height: 16px;
      letter-spacing: 0.01em;
      color: #333333;
      margin-bottom: 10px;
      span{
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        color: $blue;
        margin-left: 20px;
      }
    }
  }
}

form.checkout__form{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-between;
  margin-bottom: 100px;
  button.bold-btn{
    padding: 7px 30px;
    font-family: 'Montserrat', sans-serif;
    font-size: 13px;
    font-weight: 500;
  }
  .checkout__button-wrap{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: flex-end;
    margin-top: 30px;
  }
  .checkout__form-message{
    border: none;
    border-bottom: .5px solid #ddd;
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
    color: $blue;
    font-family: 'Montserrat', sans-serif;
    display: block;
    width: 100%;
    max-width: 100%;
    min-width: 100%;
    min-height: 80px;
    max-height: 100px;
    background: transparent;
    margin-top: 44px;
  }
  .input__wrap{
    margin-top: 28px;
  }
  .checkout__change-subtitle{
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #333;
  }
  .checkout__form-label{
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.01em;
    color: #333333;
    margin-top: 44px;
    margin-bottom: 18px;
  }
  .checkout__form-item{
    width: 32%;
  }
  .checkout__form-change{
    &.inline{
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
    }
    &__item{
      margin: 13px 22px;
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      align-items: center;
      label{
        font-weight: normal;
        font-size: 12px;
        line-height: 15px;
        color: #333;
      }
      input{
        margin-left: 12px;
        cursor: pointer;
      }
    }
  }
}


@media (max-width: 1000px) and (min-width: 801px){
  form.checkout__form{
    flex-wrap: wrap;
    .checkout__form-item{
      width: 48%;
    }
  }
  .checkout__product-item .checkout__photo .checkout__photo-img{
    width: 40px;
    height: 40px;
  }
  .checkout__product-item .checkout__name{
    font-size: 10px;
  }
  .checkout__product-item .checkout__article{
    font-size: 10px;
  }
}

@media (max-width: 800px) and (min-width: 601px){
  .checkout{
    margin-top: 130px;
  }
  form.checkout__form{
    flex-wrap: wrap;
    flex-direction: column;
    .checkout__form-item{
      width: 100%;
    }
  }
  .checkout__product-item .checkout__photo .checkout__photo-img{
    width: 40px;
    height: 40px;
    margin-left: 8px;
  }
  .checkout__product-item .checkout__name{
    font-size: 10px;
  }
  .checkout__product-item .checkout__article{
    font-size: 10px;
  }
  .checkout__product-item .checkout__price, .checkout__product-item .checkout__total{
    font-size: 11px;
  }
}


@media (max-width: 600px){
  .checkout{
    margin-top: 190px;
  }
  .bread__crumbs{
    span{
      margin-bottom: 16px;
    }
  }
  form.checkout__form{
    flex-wrap: wrap;
    flex-direction: column;
    .checkout__form-item{
      width: 100%;
    }
  }
  .checkout__product-item .checkout__photo .checkout__photo-img{
    width: 56px;
    height: 56px;
    margin-left: 12px;
  }
  .checkout__product-item .checkout__name{
    font-size: 12px;
  }
  .checkout__product-item .checkout__article{
    font-size: 12px;
  }
  .checkout__product-item .checkout__price, .checkout__product-item .checkout__total{
    font-size: 13px;
  }
  form.checkout__form .checkout__form-change__item{
    margin-left: 14px;
  }
  .checkout__product-wrap{
    overflow-x: scroll;
    padding-bottom: 20px;
    &::-webkit-scrollbar {
        height: 8px;
        width: 10%;
    }
    &::-webkit-scrollbar-thumb {
        background: $blue;
        border-radius: 10px;
    }
    table{
      width: 800px;
    }
  }
  .checkout__product-total .checkout__total-item p{
    flex-direction: column;
    font-size: 14px;
    margin-bottom: 16px;
    span{
      margin-left: 0;
      margin-top: 6px;
      font-size: 16px;
    }
  }

  form.checkout__form .checkout__button-wrap{
    justify-content: flex-start;
  }
}
