.catalog__btn{
  background: transparent;
  border: none;
  font-weight: 500;
  font-size: 13px;
  line-height: 20px;
  color: #222;
  font-family: 'Montserrat',sans-serif;
  position: relative;
  transition: .3s all ease;
  cursor: pointer;
  &:after{
    position: absolute;
    content: url(../images/arrowDown.svg);
    left: 107%;
    top: 50%;
    transform: translate(-50%,-50%) scale(1.2);
    display: block;
    transition: .3s all ease;
  }
  &:hover{
    color: $blue;
    &:after{
      transform: translate(-50%,-38%) scale(1.2);
    }
  }
}

.catalog__menu{
  margin-right: 20px;
}

.catalog__menu-wrap{
  position: relative;
  display: none;
  a.close{
    display: none;
    width: 36px;
    height: 36px;
    position: absolute;
    right: 30px;
    top: 60px;
    z-index: 1113;
  }
  &.active{
    display: block;
  }
  ul{
    position: absolute;
    top: 16px;
    left: 0;
    z-index: 1110;
    background: #fff;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
    border-radius: 4px;
    padding-top: 12px;
    width: 220px;
    li{
      margin-bottom: 12px;
      position: relative;
      padding: 0 54px 0 32px;
      cursor: pointer;
      z-index: 1;
      &:after{
        content: url(../images/arrowDown.svg);
        right: 12px;
        position: absolute;
        top: 50%;
        transform: translate(0, -60%) rotate(0deg) scale(1);
        transition: .3s all ease;
        display: none;
      }
      &.dropdown{
        &:after{
          display: block;
        }
      }
      &:hover{
        &:after{
          transform: translate(0, -45%) rotate(0deg)  scale(1);
        }
      }
      a{
        font-weight: normal;
        font-size: 11px;
        color: #333333;
        transition: .3s all ease;
        &:hover{
          color: $blue;
        }
      }
      ul{
        display: none;
        position: absolute;
        top: -12px;
        left: 100%;
        z-index: 1110;
        background: #fff;
        box-shadow: 0 0 10px rgba(0,0,0,0.1);
        border-radius: 4px;
        width: fit-content;
        height: 0;
        transition: .3s all ease;
      }
      &:hover{
        a{
          color: $blue;
        }
        &:after{
          transform: translate(0, -45%) rotate(-90deg)  scale(1.2);
        }
        ul{
          display: block;
          height: auto;
          li{
            z-index: 1;
            a{
              color: #333;
              transition: .3s all ease;
              &:hover{
                color: $blue;
              }
            }
            ul{
              display: none;
            }
            &:hover{
              a{
                color: $blue;
              }
              ul{
                display: block;
                li{
                  z-index: 1;
                  a{
                    color: #333;
                    transition: .3s all ease;
                    &:hover{
                      color: $blue;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}


@media (max-width: 1000px) and (min-width: 601px){
  .catalog__menu-wrap{
    a.close{
      display: block;
    }
    overflow-y: scroll;
    &::-webkit-scrollbar{
      width: 8px; /* ширина для вертикального скролла */
      height: 8px; /* высота для горизонтального скролла */
      margin-right: 10px;
    }
    &::-webkit-scrollbar-thumb{
      background: $blue;
      border-radius: 10px;
    }
    &.active{
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1110;
      ul{
        width: 100%;
        height: auto;
        min-height: 100%;
        top: 0;
        padding-top: 80px;
        padding-left: 30px;
        li{
          width: 300px;
          margin-bottom: 15px;
          &:hover{
            ul{
              display: none;
            }
          }
          &.active{
            ul{
              display: block;
              li{
                ul{
                  display: none;
                }
                &:hover{
                  ul{
                    display: none;
                  }
                }
                &.active{
                  ul{
                    display: block;
                  }
                }
              }
            }
            &:after{
              top: 0;
              transform: translate(0, 8px) rotate(180deg)  scale(1.5);
            }
          }
          ul{
            position: static;
            background: transparent;
            box-shadow: none;
            padding-top: 10px;
            padding-left: 0;
          }
          a{
            font-size: 15px;
            line-height: 24px;
          }
        }
      }
    }
  }
}


@media (max-width: 600px){
  .catalog__btn{
    font-weight: 400;
  }
  .catalog__menu-wrap{
    a.close{
      display: block;
    }
    overflow-y: scroll;
    padding-top: 180px;
    &::-webkit-scrollbar{
      width: 8px; /* ширина для вертикального скролла */
      height: 8px; /* высота для горизонтального скролла */
      margin-right: 10px;
    }
    &::-webkit-scrollbar-thumb{
      background: $blue;
      border-radius: 10px;
    }
    display: none;
    opacity: 0;
    &.active{
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1110;
      animation: showMenu 1s;
      display: block;
      opacity: 1;
      ul{
        width: 100%;
        height: auto;
        min-height: 100%;
        top: 0;
        padding: 140px 30px;
        li{
          width: 300px;
          margin-bottom: 15px;
          padding: 0;
          &:hover{
            ul{
              display: none;
            }
          }
          &.active{
            border-bottom: .5px solid #ddd;
            border-top: .5px solid #ddd;
            padding-top: 10px;
            ul{
              display: block;
              li{
                ul{
                  display: none;
                }
                &:hover{
                  ul{
                    display: none;
                  }
                }
                &.active{
                  ul{
                    display: block;
                  }
                }
              }
            }
            &:after{
              top: 0;
              transform: translate(0, 8px) rotate(180deg)  scale(1.5);
            }
          }
          ul{
            position: static;
            background: transparent;
            box-shadow: none;
            padding-top: 10px;
            padding-left: 0;
            padding-bottom: 0;
            margin: 28px 0;
          }
          a{
            font-size: 15px;
            line-height: 24px;
          }
        }
      }
    }
  }
}

@keyframes showMenu{
  0%{
    opacity: 0;
    display: none;
  }
  10%{
    opacity: 0;
    display: block;
  }
  100%{
    opacity: 1;
    display: block;
  }
}
