.profile__inf-item{
  display: none;
  &.active{
    display: block;
  }
}

.profile{
  &__wrap{
    width: 100%;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: space-between;
  }
  &__user{
    width: 32%;
    &-inf{
      img{
        display: block;
        width: 54px;
        height: 54px;
        border-radius: 50%;
        margin-bottom: 28px;
      }
    }
    &-menu{
      ul{
        li{
          a{
            padding: 18px 6px;
            display: block;
            font-weight: normal;
            font-size: 15px;
            line-height: 18px;
            letter-spacing: 0.01em;
            color: #333333;
            transition: .3s all ease;
            position: relative;
            &:after{
              content: '';
              width: 70%;
              height: .5px;
              background: #ddd;
              left: 0;
              top: 0;
              position: absolute;
            }
            &:hover{
              color: $blue;
            }
            &.active{
              color: $blue;
            }
          }
        }
      }
    }
    a.profile__user-name{
      padding-left: 0;
      font-weight: 500;
      font-size: 20px;
      line-height: 24px;
      letter-spacing: 0.01em;
      color: #333333;
      margin-bottom: 28px;
      display: block;
      width: fit-content;
      &:after{
        display: none;
        transform: translate(-50%, -10%) rotate(180deg) scale(1.5);
      }
      &:hover{
        &:after{
          transform: translate(-50%, -30%) rotate(180deg) scale(1.5);
        }
      }
      &.active{
        &:after{
          transform: translate(-50%, -50%) rotate(0deg) scale(1.5);
        }
        &:hover{
          &:after{
            transform: translate(-50%, -70%) rotate(0deg) scale(1.5);
          }
        }
      }
    }
  }
}

.profile{
  &__inf{
    width: 64%;
  }
  &__card{
    width: 100%;
    padding: 44px;
    background: #fff;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
    border-radius: 4px;
    box-sizing: border-box;
    h3.title{
      font-weight: 500;
      font-size: 22px;
      line-height: 27px;
      letter-spacing: 0.01em;
      color: #222222;
      margin-bottom: 34px;
    }
  }
  form.profile__form{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    button.bold-btn{
      width: fit-content;
      padding: 10px 20px;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      letter-spacing: 0.01em;
      color: #FFFFFF;
      font-family: 'Montserrat', sans-serif;
      margin-right: 20px;
      cursor: pointer;
    }
  }
  .profile__form-wrap{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .input__wrap{
      margin: 0 20px 48px 20px;
      display: block;
      input{
        width: 100%;
      }
      &.small{
        width: 26%;
      }
      &.medium{
        width: 36%;
      }
    }
  }
}

.input__wrap{
  .error{
    color: #dc2f09;
    font-size: 13px;
  }
  input{
    background: 0 0;
    border: none;
    border-bottom: .5px solid #ddd;
    line-height: 17px;
    padding: 10px 48px 10px 10px;
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.01em;
    color: #686DE8;
    font-family: 'Montserrat', sans-serif;
    transition: .3s all ease;
    &::placeholder{
      color: #666 !important;
      font-family: 'Montserrat', sans-serif;
      font-weight: 400;
    }
    &:focus{
      border-color: $blue;
    }
  }
  label{
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: 0.01em;
    color: #333333;
    font-family: 'Montserrat', sans-serif;
    display: block;
  }
}

.card__order{
  width: 100%;
  padding: 18px;
  background: #fff;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  border-radius: 4px;
  box-sizing: border-box;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-between;
  margin-bottom: 18px;
  &-main{
    h2.title{
      font-weight: 500;
      font-size: 15px;
      line-height: 18px;
      color: #222222;
      margin-bottom: 14px;
    }
  }
  &-wrap{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
  }
  &-img{
    width: 64px;
    height: 64px;
    background-color: #F5F7FF;
    background-size: 85%;
    background-position: center;
    background-repeat: no-repeat;
  }
  &-inf{
    margin-left: 18px;
    p{
      margin-bottom: 7px;
      font-weight: 500;
      font-size: 12px;
      line-height: 15px;
      color: #333333;
      font-family: 'Montserrat', sans-serif;
      span{
        color: $blue;
      }
    }
  }
  &-price{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
    .price{
      font-size: 16px;
    }
    a.bold-btn{
      padding: 7px 16px;
      font-weight: 600;
      font-size: 12px;
      line-height: 15px;
      color: #FFFFFF;
      font-family: 'Montserrat', sans-serif;
    }
  }
}

.card__history{
  width: 100%;
  padding: 18px;
  background: #fff;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  border-radius: 4px;
  box-sizing: border-box;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-between;
  margin-bottom: 18px;
  &-img{
    width: 64px;
    height: 64px;
    background-color: #F5F7FF;
    background-size: 85%;
    background-position: center;
    background-repeat: no-repeat;
  }
  &-main{
    h2.title{
      font-weight: 500;
      font-size: 15px;
      line-height: 18px;
      color: #222222;
      margin-bottom: 14px;
    }
  }
  &-wrap{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
  }
  &-inf{
    margin-left: 18px;
    p{
      margin-bottom: 7px;
      font-weight: 500;
      font-size: 12px;
      line-height: 15px;
      color: #333333;
      font-family: 'Montserrat', sans-serif;
      span{
        color: $blue;
      }
    }
  }
  &-price{
    .price{
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
    }
  }
}

.card__favourite{
  width: 100%;
  padding: 18px;
  background: #fff;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  border-radius: 4px;
  box-sizing: border-box;
  margin-bottom: 18px;
  &-img{
    width: 64px;
    height: 64px;
    background-color: #F5F7FF;
    background-size: 85%;
    background-position: center;
    background-repeat: no-repeat;
  }
  &-wrap{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: space-between;
  }
  &-header{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: space-between;
    h2.title{
      font-weight: 500;
      font-size: 15px;
      line-height: 18px;
      color: #222222;
      margin-bottom: 14px;
    }
    a.close{
      width: 20px;
      height: 20px;
      &:before, &:after{
        width: 20px;
        height: 1px;
      }
    }
  }
  &-main{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
  }
  &-price{
    margin-left: 18px;
    .price{
      font-weight: 600;
      font-size: 18px;
      &__last{
        font-size: 14px;
        line-height: 17px;
        margin-bottom: 0;
      }
    }
  }
  .count{
    &:after{
      display: none;
    }
    .label{
      font-size: 12px;
      margin-bottom: 0;
    }
    input{
      font-size: 16px;
    }
    button.minus{
      left: 30%;
      transform: translate(-50%, -55%);
    }
    button.plus{
      right: 12%;
      transform: translate(-50%, -50%);
    }
  }
  &-add__cart{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    a.bold-btn{
      font-weight: 500;
      font-size: 12px;
      line-height: 15px;
      color: #FFFFFF;
      padding: 4px 20px;
    }
  }
}

a.close{
  width: 36px;
  height: 36px;
  position: relative;
  &:after, &:before{
    width: 36px;
    height: 1px;
    content: '';
    background: #ccc;
    position: absolute;
    top: 50%;
    left: 50%;
    transition: .3s all ease;
  }
  &:hover{
    &:after, &:before{
      background: $blue;
    }
  }
  &:after{
    transform: translate(-50%, -50%) rotate(45deg);
  }
  &:before{
    transform: translate(-50%, -50%) rotate(-45deg);
  }
}

.input__wrap{
  input{
    color: $blue;
    &::placeholder{
      color: #666;
    }
  }
}


@media (max-width: 1300px) and (min-width: 1001px){
  .profile__form-wrap{
    .input__wrap{
      width: 40% !important;
    }
  }
}


@media (max-width: 1000px) and (min-width: 801px){
  .profile__form-wrap{
    .input__wrap{
      width: 100% !important;
      margin-bottom: 20px;
    }
  }
  .card__order-main h2.title{
    font-size: 13px;
  }
  .card__order-price a.bold-btn{
    font-size: 10px;
  }
  .card__order-inf p{
    font-size: 11px;
  }
}


@media (max-width: 800px) and (min-width: 601px){
  .profile__wrap{
    flex-direction: column;
  }
  .profile__user{
    width: 100%;
  }
  .profile__inf{
    width: 100%;
    margin-top: 30px;
  }
  .profile__form-wrap{
    .input__wrap{
      width: 100% !important;
      margin-bottom: 20px;
    }
  }
  .card__order-main h2.title{
    font-size: 13px;
  }
  .card__order-price a.bold-btn{
    font-size: 10px;
  }
  .card__order-inf p{
    font-size: 11px;
  }
  .profile__user-menu ul li a:after{
    width: 100%;
  }
}

@media (max-width: 600px){
  .profile{
    margin-top: 150px;
  }
  .profile__wrap{
    flex-direction: column;
  }
  .profile__user{
    width: 100%;
  }
  .profile__inf{
    width: 100%;
    margin-top: 30px;
  }
  .profile__form-wrap{
    .input__wrap{
      width: 100% !important;
      margin: 0;
      margin-bottom: 20px;
    }
  }
  .card__order-main h2.title{
    font-size: 13px;
  }
  .card__order-price a.bold-btn{
    font-size: 12px;
  }
  .card__order-inf p{
    font-size: 11px;
  }
  .profile__user-menu ul li a:after{
    width: 100%;
  }
  .card__order{
    flex-direction: column;
  }
  .card__order-price{
    flex-direction: row;
    margin-top: 16px;
  }
  .profile__card{
    padding: 10px;
  }
  .profile__card h3.title{
    font-size: 22px;
    padding-left: 10px;
    padding-top: 10px;
  }
  .card__favourite-wrap{
    flex-direction: column;
  }
  .card__favourite-add__cart{
    flex-direction: row;
    align-items: center;
    margin-top: 16px;
  }
  .card__favourite .count button.plus{
    transform: translate(40%,-50%);
  }
  .card__favourite .count button.minus{
    transform: translate(-130%,-55%);
  }
  .card__history{
    flex-direction: column;
  }
  .card__history-price{
    margin-top: 14px;
  }
  .card__order-price .price{
    font-weight: 600;
  }
  .input__wrap{
    input{
      font-size: 16px;
      &::placeholder{
        font-size: 16px;
      }
    }
    input[type="password"]{
      font-size: 16px !important;
      &::placeholder{
        font-size: 16px;
      }
    }
  }
}
